
html {
  height: 100%;
}
body {
  background: url(/static/img/background1.76d07e1.jpg);
  background-position-x: 40%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 10px;
  text-align: center;
  color: black;
  margin: 0;
}
@media screen and (min-device-width: 600px){
body {
    background-size:100%;
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-size: 16px;
}
}
#app {
  margin: 0px;
  height: 100%;
}
.tabs {
  height: 10%;
  background-color: black;
}
.content {
  height: 90%;
  background-color: bisque;
}
ul {
  height:100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
li {
  height: 100%;
  margin-left: 2%;
  margin-right: 2%;
  list-style: none;
  border: 1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
a {
  height: 100%;
  color: whitesmoke;
  text-decoration: none;
  font-size: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.opacity60 {
    filter: alpha(opacity : 60);
    opacity:0.6;
    -moz-opacity: 0.6;
    -khtml-opacity:0.6;
}
.opacity70 {
    filter: alpha(opacity : 70);
    opacity: 0.7;
    -moz-opacity: 0.7;
    -khtml-opacity: 0.7;
}
.opacity90 {
    filter: alpha(opacity : 90);
    opacity:0.9;
    -moz-opacity: 0.9;
    -khtml-opacity:0.9;
}

#name {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 5.5em;
}
#name span {
  cursor: pointer;
}

#list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 0;
  padding: 0;
  background-color: dimgray;
}
#list li {
  font-family: "Arial","Microsoft YaHei","\9ED1\4F53","\5B8B\4F53",sans-serif;
  margin: 10px;
  color:white;
  font-size: 2em;
  cursor: pointer;
  /* font-weight: 550; */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

#snap {
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-orient:horizontal;
  -webkit-box-direction:normal;
      -ms-flex-direction:row;
          flex-direction:row;
  font-weight:600;
}
#cebian{
  width:20%;
}
#self {
  font-size:2rem;
}
#self img{
  margin:20% 20% 0% 20%;
  width:60%;
  height:60%;
}
progress
{
  width: 168px;
  height: 5px;
}
progress::-webkit-progress-bar
{
  background-color:#d7d7d7;
}
progress::-webkit-progress-value
{
  background-color:orange;
}

h1,h2,h3,h4,h5,p {
  margin:0;
}
#c {
  overflow: auto;
  padding-left:10%;
  padding-right: 10%;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  background-color: bisque;
  margin-bottom: 5%;
  text-align: start;
}
